
.toggle-button-group {
  display: flex;
  justify-content: stretch;
  width: 80vw;
  border: 1px solid;
  border-color: #E0E0E0;
  border-radius: 0.25em;
  overflow: hidden;
}

.toggle-button {
  /* clear default styles */
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  
  /* custom styling */
  flex: 1;
  background-color: #FCFCFC;
  border: 1px solid;
  border-radius: 0.25em;
  border-color: #E0E0E0;
  padding: 0.5em 0;
}

.toggle-button-group .toggle-button {
  border: unset;
  border-radius: 0;
}

.toggle-button:not(:last-child) {
  border-right: 1px solid;
  border-color: #E0E0E0;
}

.toggle-button:active {
  background-color: #CCC;
}

.toggle-button-selected {
  background-color: #E0E0E0;
}
